
.carousel {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.carousel .items-container {
    height: 100%;
}
.carousel .items-container .item-img {
      float: left;
      width: 100%;
      height: 100%;
      background-position: top center;
      background-repeat: no-repeat;
      position: relative;
}
.carousel .prev,
  .carousel .next {
    cursor: pointer;
    position: absolute;
    z-index: 999;
    top: 50%;
    width: 44px;
    margin-top: -40px;
    text-align: center;
    font-size: 60px;
    font-weight: 100;
    line-height: 80px;
}
.carousel .prev:hover,
    .carousel .next:hover {
      color: #ccc;
      background: rgba(0, 0, 0, 0.3);
}
.carousel .prev {
    left: 2em;
}
.carousel .next {
    right: 2em;
}
.carousel .dots {
    position: absolute;
    bottom: 40px;
    left: 50%;
    z-index: 999;
}
.carousel .dots .dot {
      cursor: pointer;
      box-sizing: border-box;
      border: 2px solid transparent;
      border-radius: 50%;
      width: 12px;
      height: 12px;
      background: #333;
      margin: 6px;
      float: left;
}
.carousel .dots .dot.active-dot {
        border: 2px solid #f84e4e;
}



