
.carousel-3d {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  background: #15161a;
}
.carousel-3d .dots {
    position: absolute;
    bottom: 12px;
    left: 50%;
    z-index: 999;
}
.carousel-3d .dots .dot {
      cursor: pointer;
      box-sizing: border-box;
      border: 2px solid transparent;
      border-radius: 50%;
      width: 12px;
      height: 12px;
      background: #333;
      margin: 6px;
      float: left;
}
.carousel-3d .dots .dot.active-dot {
        border: 2px solid #f84e4e;
}
.carousel-3d .list-container .item-container {
    display: inline-block;
    position: absolute;
    transition: left 1s, transform 1s;
}
.carousel-3d .list-container .item-container:hover a.img-title {
      color: #00c0ff;
}
.carousel-3d .list-container .item-img {
    display: block;
}
.carousel-3d .list-container a.img-title {
    width: 100%;
    background: #222328;
    text-align: center;
    font-size: 16px;
    line-height: 36px;
    position: absolute;
    z-index: 100;
    top: 100%;
    left: 0;
}



