
.right-service .service-container {
  box-sizing: border-box;
  width: 420px;
  height: 470px;
  background: #15161a;
  padding: 20px 20px 0 20px;
}
.right-service .search-bar > * {
  box-sizing: border-box;
  vertical-align: middle;
  height: 35px;
}
.right-service .search-bar > .search-input {
  border: 1px solid #000;
  background: #111214;
  width: 244px;
  outline: none;
  color: #999;
  text-indent: 10px;
}
.right-service .search-bar > .search-btn {
  border-radius: 4px;
  background-color: #ed4c4c;
}
.right-service .search-bar > .search-btn:hover {
    background-color: #f76161;
}
.right-service .search-bar .ask-question-btn {
  width: 90px;
  color: #ccc;
  background-color: #565864;
  border-radius: 2px;
  margin-left: 10px;
  line-height: 35px;
  text-align: center;
}
.right-service .search-bar .ask-question-btn:hover {
    background-color: #6a6c77;
}
.right-service .questions {
  margin-top: 26px;
}
.right-service .questions .questions-title {
    display: inline-block;
    width: 380px;
    padding-bottom: 10px;
    font-size: 16px;
    color: #ccc;
    border-bottom: 1px solid #333;
}
.right-service .questions .questions-list {
    margin-top: 18px;
}
.right-service .questions .questions-list .questions-item:first-child a {
      color: #ed4c4c;
}
.right-service .questions .questions-list .questions-item:first-child a .dot-icon {
        background-position: -3px -825px;
}
.right-service .questions .questions-list .questions-item .questions-item-a {
      line-height: 36px;
      color: #ccc;
}
.right-service .questions .questions-list .questions-item .questions-item-a:hover {
        color: #00c0ff;
}
.right-service .questions .questions-list .questions-item .questions-item-a:hover .dot-icon {
          background-position: -3px -814px;
}
.right-service .questions .questions-list .questions-item .questions-item-a .questions-text {
        display: inline-block;
        vertical-align: middle;
}



