
.btn .opacity-transition {
  opacity: 0;
  transition: opacity 1s;
}
.btn .opacity-transition:hover {
    opacity: 1;
}
.left-btns .btn,
.left-btns .btn .opacity-transition {
  display: inline-block;
  width: 246px;
  height: 93px;
  background: url("/static/img/body/btns/u_left_buttons.png") no-repeat;
  margin-right: 6px;
}
.left-btns .btn1 .opacity-transition {
  background-position: 0 -94px;
}
.left-btns .btn2 {
  background-position: -252px 0;
}
.left-btns .btn2 .opacity-transition {
    background-position: -252px -94px;
}
.left-btns .btn3 {
  background-position: -508px 0;
  margin-right: 0;
}
.left-btns .btn3 .opacity-transition {
    background-position: -508px -94px;
}



