
.left-activity-item {
  position: relative;
  width: 668px;
  height: 444px;
}
.left-activity-item .activity-item-item {
    position: relative;
}
.left-activity-item .activity-item-item:first-child, .left-activity-item .activity-item-item:nth-child(3) {
      margin-right: 40px;
}
.left-activity-item .activity-item-item .title {
      font-size: 16px;
      margin-top: 12px;
      margin-bottom: 34px;
}
.left-activity-item .activity-item-item .item-item-img {
      width: 310px;
      height: 150px;
      border: 1px solid #000;
}
.left-activity-item .activity-item-item .item-item-img:hover {
        border: 1px solid #00c0ff;
}
.left-activity-item .activity-item-item .state {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 60px;
      line-height: 24px;
      text-align: center;
}
.left-activity-item .activity-item-item .state.valid {
        background: rgba(114, 202, 42, 0.7);
        color: #fff;
}
.left-activity-item .activity-item-item .state.invalid {
        background: rgba(102, 102, 102, 0.7);
}
.activity-more {
  width: 114px;
  height: 28px;
  background: url("/static/img/body/activity/more-bg.png") no-repeat;
  display: inline-block;
  position: absolute;
  left: 50%;
  margin-left: -59px;
  bottom: 0px;
}
.activity-more:hover {
    background-position: 0 -28px;
}



