
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
button,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
body {
  color: #999;
  font-size: 14px;
  font-family: "Microsoft YaHei", Verdana, Arial, Helvetica, sans-serif;
  line-height: 1;
}
a {
  color: #999;
  text-decoration: none;
}
li {
  list-style: none;
}
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clearfix {
  *zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.horizontal > * {
  display: inline-block;
}
.vertical-center {
  display: table;
}
.vertical-center > *:first-child {
    display: table-cell;
    vertical-align: middle;
}
.icon-text-hor > * {
  vertical-align: middle;
}
.header-logo-icon {
  display: inline-block;
  width: 260px;
  height: 40px;
  background: url("/static/img/header/dota2.home.logo.png") no-repeat;
}
.header-user-avtar-icon {
  display: inline-block;
  width: 50px;
  height: 50px;
  background: url("/static/img/icons-1.png") no-repeat;
  background-position: 13px -451px;
}
.hot-icon {
  transform: scale(0.65);
  color: #fff;
  line-height: 18px;
  padding: 0 6px;
  border-radius: 10px;
  background: #ed4c4c;
}
.hot-icon:after {
    content: 'HOT';
}
.search-icon {
  display: inline-block;
  width: 35px;
  height: 35px;
  background: url("/static/img/icons.png") no-repeat;
  background-position: 6px -692px;
}
.dot-icon {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: url("/static/img/icons.png") no-repeat;
  background-position: -3px -803px;
  margin-right: 10px;
  vertical-align: middle;
}
.wanmei-logo {
  display: inline-block;
  width: 94px;
  height: 33px;
  background: url("/static/img/footer/logo-wanmei.png") no-repeat;
}
.valve-logo {
  display: inline-block;
  width: 99px;
  height: 28px;
  background: url("/static/img/footer/logo-valve.png") no-repeat;
}
.service-icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  background: url("/static/img/slide/icon-float-bar.png") no-repeat;
  background-position: -8px -10px;
  vertical-align: middle;
}
.weibo-icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  background: url("/static/img/slide/icon-float-bar.png") no-repeat;
  background-position: -42px -10px;
  vertical-align: middle;
}
body {
  background: #1a1b1f;
  background-image: url("/static/img/dot2.main.bg.png");
  background-size: cover;
}
body a {
    cursor: pointer;
}



