
.left-news-item {
  width: 750px;
  color: #999;
  position: absolute;
}
.left-news-item .first-news {
    cursor: pointer;
    margin: 0 5px 15px 5px;
    padding: 15px 40px 15px 20px;
    border: 1px solid transparent;
}
.left-news-item .first-news:hover {
      border: 1px solid #00c0ff;
      box-shadow: 0 0 8px #00c0ff;
}
.left-news-item .first-news .first-news-title {
      font-size: 24px;
      line-height: 1;
      color: #00c0ff;
      margin-bottom: 22px;
}
.left-news-item .first-news .first-news-content {
      overflow: hidden;
}
.left-news-item .first-news .first-news-content > * {
        float: left;
}
.left-news-item .first-news .first-news-img {
      width: 200px;
      height: 120px;
      margin-right: 20px;
}
.left-news-item .first-news .first-news-summary {
      line-height: 1.6;
      width: 458px;
}
.left-news-item .first-news .first-news-summary .date {
        margin-top: 10px;
        font-size: 12px;
}
.left-news-item .other-news-title {
    cursor: pointer;
    padding: 0 40px 0 20px;
    box-sizing: border-box;
    font-size: 16px;
    font-weight: normal;
    color: #ccc;
    line-height: 45px;
    height: 45px;
    vertical-align: middle;
}
.left-news-item .other-news-title:nth-child(odd) {
      background: #18191c;
}
.left-news-item .other-news-title:hover {
      color: #00c0ff;
}
.left-news-item .other-news-title:hover .dot-icon {
        background-position: -3px -814px;
}
.left-news-item .other-news-title .date {
      float: right;
      font-size: 12px;
}



