
.right-map {
  margin-top: 20px;
}
.right-map .map-content .map-item {
    width: 200px;
    height: 120px;
}
.right-map .map-content .map-item:hover img {
      border: 1px solid #00c0ff;
}
.right-map .map-content .map-item:hover .map-title {
      color: #00c0ff;
}
.right-map .map-content .map-item:first-child {
      margin-right: 20px;
}
.right-map .map-content .map-item img {
      display: block;
      border: 1px solid transparent;
      box-sizing: border-box;
}
.right-map .map-content .map-title {
    text-align: center;
    line-height: 38px;
    background: #222328;
}



