
.repeat-nav > li {
  cursor: pointer;
  position: relative;
  box-sizing: border-box;
  line-height: 36px;
  text-align: center;
  border: 1px solid #333;
  border-right: 1px solid transparent;
}
.repeat-nav > li:last-child {
    border-right: 1px solid #333;
}
.repeat-nav > li:hover, .repeat-nav > li.active {
    border: 1px solid #999;
    color: #ccc;
    position: relative;
    z-index: 1;
    transition: border .5s;
}
.repeat-nav > li .hot {
    position: absolute;
    top: 1px;
    right: 18px;
}



