
.left-activity-nav {
  position: absolute;
}
.left-activity-nav > li {
    cursor: pointer;
    width: 140px;
    text-align: center;
    border-bottom: 1px solid transparent;
    box-sizing: border-box;
    height: 40px;
}
.left-activity-nav > li:hover, .left-activity-nav > li.active {
      border-bottom: 1px solid #00c0ff;
}
.left-activity-nav > li:last-child a {
      border-right: none;
}
.left-activity-nav > li a {
      position: relative;
      display: inline-block;
      width: 100%;
      line-height: 20px;
      border-right: 1px solid #999;
}
.left-activity-nav > li:hover a,
    .left-activity-nav > li.active a {
      color: #00c0ff;
}
.left-activity-nav > li .hot {
      position: absolute;
      top: -8px;
      right: 2px;
}



