
.left-activity-item-shop {
  width: 668px;
  height: 444px;
  position: relative;
}
.left-activity-item-shop .shop-item-item {
    width: 300px;
    height: 376px;
    position: relative;
    vertical-align: top;
}
.left-activity-item-shop .shop-item-item:first-child {
      padding-right: 31px;
      border-right: 1px solid #333;
}
.left-activity-item-shop .shop-item-item:nth-child(2) {
      margin-left: 32px;
}
.left-activity-item-shop .shop-item-item .item-item-img {
      width: 300px;
      height: 200px;
}
.left-activity-item-shop .shop-item-item .title {
      font-size: 16px;
      margin-top: 23px;
      color: #ccc;
}
.left-activity-item-shop .shop-item-item .price {
      color: #a93827;
      margin-top: 8px;
      font-size: 16px;
}
.left-activity-item-shop .shop-item-item .info {
      height: 24px;
      margin-top: 24px;
      color: #666;
      margin-bottom: 20px;
}
.left-activity-item-shop .shop-item-item .shop-logo {
      position: absolute;
      top: 1px;
      left: 1px;
      width: 102px;
      height: 33px;
      background: url("/static/img/body/activity/shop.png") no-repeat;
}
.left-activity-item-shop .shop-item-item .shop-logo.logo2 {
        background-position: 2px -66px;
}
.left-activity-item-shop .shop-item-item .shop-btn {
      height: 43px;
      width: 100%;
      font-size: 18px;
      color: #d3cbc7;
      line-height: 43px;
      text-align: center;
      cursor: pointer;
      background: url("/static/img/body/activity/activities-shop-btn.jpg") 0px -43px no-repeat;
}
.left-activity-item-shop .shop-item-item .shop-btn:hover {
        background-position: 0 0;
        color: #fff;
}



