
@charset "UTF-8";
.left-video-item {
  width: 100%;
  height: 100%;
  padding: 23px 34px 0 34px;
  position: relative;
}
.left-video-item .video-item-item {
    position: relative;
    box-sizing: border-box;
    padding: 10px;
    margin: 2px;
    border: 1px solid transparent;
    transition: border .2s, color .2s;
}
.left-video-item .video-item-item:hover {
      color: #00c0ff;
      border: 1px solid #00c0ff;
      box-shadow: 0 0 5px #00c0ff;
}
.left-video-item .video-item-item:hover:after {
        position: absolute;
        content: "";
        top: 40px;
        left: 80px;
        display: inline-block;
        width: 54px;
        height: 54px;
        background: url("/static/img/icons.png") no-repeat;
        background-position: -3px -1099px;
}
.left-video-item .video-item-item > .item-img {
      display: inline-block;
      width: 200px;
      height: 117px;
      overflow: hidden;
      position: relative;
}
.left-video-item .video-item-item > .item-img:after {
        content: '视频';
        position: absolute;
        right: 0;
        bottom: 0;
        width: 35px;
        line-height: 20px;
        background: rgba(3, 4, 12, 0.8);
}
.left-video-item .video-item-item > .item-img img {
        width: 200px;
        height: 117px;
}
.left-video-item .video-item-item > .item-title {
      width: 200px;
      text-align: center;
      line-height: 18px;
      margin-top: 8px;
      margin-bottom: 8px;
}



