
.header-user {
  height: 50px;
  margin: 15px;
}
.header-user .header-user-avtar-icon {
    background-color: #101215;
    border-radius: 50%;
    border: 1px solid #262323;
}
.header-user .user-login {
    margin-left: 15px;
    line-height: 24px;
}
.header-user .user-login .login-text {
      font-size: 18px;
}
.header-user .user-login .login-text a {
        color: #00c0ff;
}
.header-user .user-login .login-text a:hover {
          text-decoration: underline;
}
.header-user .user-login .login-tip {
      font-size: 12px;
      line-height: 17px;
}



