
.my-slide {
  position: fixed;
  z-index: 9999;
  top: 50%;
  right: 0;
  margin-top: -206px;
  width: 135px;
  height: 412px;
  background: #2c3437;
  font-size: 16px;
  text-align: center;
  transition: right 1s;
}
.my-slide a.btn {
    box-sizing: border-box;
    width: 27px;
    height: 68px;
    background: #ac3c26;
    position: absolute;
    right: 100%;
    color: #fff;
    padding-top: 10px;
}
.my-slide a.btn span {
      display: block;
}
.my-slide a.btn .close-icon {
      font-size: 19px;
}
.my-slide .online-service,
  .my-slide .sina-weibo {
    background: url("/static/img/slide/bg-rightbar-box.png") repeat-x;
    border-bottom: 1px solid #47494d;
    box-sizing: border-box;
    font-size: 12px;
}
.my-slide .online-customer-service:hover {
    background: #3f4a4e;
}
.my-slide .online-customer-service:hover .service-icon {
      background-position: -8px -53px;
}
.my-slide .sina-weibo-container:hover {
    background: #3f4a4e;
}
.my-slide .sina-weibo-container:hover .weibo-icon {
      background-position: -42px -51px;
}
.my-slide .online-customer-service,
  .my-slide .sina-weibo-container {
    text-align: center;
    display: block;
    margin-top: 16px;
    box-sizing: border-box;
    width: 90px;
    line-height: 30px;
    margin-left: 22px;
    border: 1px solid #525e63;
    color: #fff;
}
.my-slide .online-customer-service .service-text,
    .my-slide .sina-weibo-container .service-text {
      vertical-align: middle;
}
.my-slide .online-service {
    overflow: hidden;
    height: 95px;
}
.my-slide .online-service .e-mail .e-mail-text {
      line-height: 24px;
}
.my-slide .online-service .e-mail .e-mail-address {
      display: inline-block;
      width: 135px;
      height: 19px;
      background: url("/static/img/slide/pwrd_email.png") no-repeat;
}
.my-slide .sina-weibo {
    height: 180px;
    overflow: hidden;
}
.my-slide .sina-weibo .weixing-text {
      line-height: 30px;
}
.my-slide .sina-weibo .weixing-2d-code {
      display: inline-block;
      width: 90px;
      height: 90px;
      background: url("/static/img/slide/icon-sina-qr.png") no-repeat;
      background-size: 90px 90px;
}
.my-slide .dota-aide {
    margin-top: -1px;
    display: inline-block;
    width: 135px;
    height: 139px;
    background: url("/static/img/slide/img-dota2app-qr-new.png") no-repeat;
}
.my-slide .dota-aide:hover {
      background-position: -135px 1px;
}



