
.header-nav-item {
  width: 92px;
  height: 80px;
  position: relative;
  text-align: center;
}
.header-nav-item .titleCn {
    font-size: 16px;
}
.header-nav-item .titleEn {
    font-size: 12px;
    transform: scale(0.75);
    line-height: 16px;
    margin-top: 2px;
}
.header-nav-item .header-nav-item-list {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 9;
    width: 100%;
    height: 240px;
    background: url("/static/img/header/level2.nav.bg.png") no-repeat;
    background-size: cover;
    color: #827c79;
}
.header-nav-item .header-nav-item-list li {
      font-size: 15px;
      line-height: 2;
}
.header-nav-item .header-nav-item-list li > a:hover {
        color: #00c0ff;
}



