
.right-hot .video {
  width: 420px;
  height: 202px;
  overflow: hidden;
  position: relative;
}
.right-hot .video video {
    width: 420px;
    background-color: #15242c;
}
.right-hot .video .video-title {
    display: inline-block;
    width: 420px;
    height: 202px;
    background: url("/static/img/body/news/video_bg.png") no-repeat;
    position: absolute;
    left: 0;
    top: 0;
}
.right-hot .video .video-title img {
      position: absolute;
      left: 50%;
      margin-left: -125px;
      bottom: 8px;
}



