
.icon-title {
  line-height: 1;
  height: 35px;
  font-size: 18px;
  margin-bottom: 10px;
  color: #fff;
}
.icon-title .title {
    display: inline-block;
    margin-left: 4px;
    line-height: 35px;
    font-size: 18px;
}
.icon-title .news-center-icon {
    display: inline-block;
    width: 35px;
    height: 35px;
    background: url("/static/img/icons.png") no-repeat;
    background-position: -3px -1px;
}
.icon-title .recent-hot-pots-icon {
    display: inline-block;
