    height: 35px;
    background: url("/static/img/icons.png") no-repeat;
    background-position: -3px -127px;
}
.icon-title .activity-center-icon {
    display: inline-block;
    width: 35px;
    height: 35px;
    background: url("/static/img/icons.png") no-repeat;
    background-position: -3px -85px;
}
.icon-title .version-updating-icon {
    display: inline-block;
    width: 35px;
    height: 35px;
    background: url("/static/img/icons.png") no-repeat;
    background-position: -3px -127px;
}
.icon-title .self-map-icon {
    display: inline-block;
    width: 35px;
