    background: url("/static/img/icons.png") no-repeat;
    background-position: -3px -253px;
}
.icon-title .more {
    float: right;
    font-size: 12px;
    color: #999;
    margin-top: 10px;
}
.icon-title .more:hover {
      color: #00c0ff;
}
.icon-title .more:hover .more-icon {
      background-position: -3px -774px;
}
.icon-title .more .more-icon {
      display: inline-block;
      width: 10px;
      height: 10px;
      background: url("/static/img/icons.png") no-repeat;
      background-position: -3px -787px;
      margin-left: 4px;
}



