
.right-btns {
  height: 95px;
}
.right-btns .btn,
  .right-btns .btn .opacity-transition {
    display: inline-block;
    width: 140px;
    height: 95px;
    background: url("/static/img/body/btns/u_right_buttons.png") no-repeat;
}
.right-btns .btn1 .opacity-transition {
    background-position: 0 -96px;
}
.right-btns .btn2 {
    background-position: -140px 0;
}
.right-btns .btn2 .opacity-transition {
      background-position: -140px -96px;
}
.right-btns .btn3 {
    background-position: -280px 0;
}
.right-btns .btn3 .opacity-transition {
      background-position: -280px -96px;
}



